"use client"

import Link from "next/link"
import Image from "next/image"
import { Button } from "@/components/ui/button"
import { useState } from "react"

export function Navigation() {
  const [isProgramsOpen, setIsProgramsOpen] = useState(false)

  return (
    <nav className="fixed top-4 left-0 right-0 z-50 px-4 sm:px-6 lg:px-8">
      <div className="container mx-auto max-w-7xl">
        <div className="bg-white rounded-full px-6 py-4 shadow-lg flex items-center justify-between">
          {/* Logo */}
          <Link href="/" className="flex items-center gap-2">
            <Image
              src="/mendiv.png"
              alt="Mendiv"
              width={140}
              height={50}
              className="h-8 md:h-10 w-auto"
            />
          </Link>

          {/* Center Navigation Links with Dividers */}
          <div className="hidden lg:flex items-center gap-0 absolute left-1/2 transform -translate-x-1/2">
            <Link
              href="/"
              className="text-sm font-medium text-black px-4"
            >
              Home
            </Link>
            <span className="text-white/30">|</span>
            <Link
              href="/about"
              className="text-sm font-medium text-black px-4"
            >
              About
            </Link>
            <span className="text-white/30">|</span>
            <div
              className="relative"
              onMouseEnter={() => setIsProgramsOpen(true)}
              onMouseLeave={() => setIsProgramsOpen(false)}
            >
              <Link
                href="/programs"
                className="text-sm font-medium text-black px-4"
              >
                Programs
              </Link>
              {isProgramsOpen && (
                <div className="absolute top-full left-0 pt-2 bg-transparent">
                  <div className="bg-white rounded-lg shadow-lg py-2 min-w-[200px] z-50">
                    <Link
                      href="/lifescript"
                      className="block px-4 py-2 text-sm text-gray-700"
                    >
                      LifeScript
                    </Link>
                    <Link
                      href="/neurosleep"
                      className="block px-4 py-2 text-sm text-gray-700"
                    >
                      NeuroSleep
                    </Link>
                    <Link
                      href="/bioalign"
                      className="block px-4 py-2 text-sm text-gray-700"
                    >
                      BioAlign
                    </Link>
                    <Link
                      href="/biobrain-studio"
                      className="block px-4 py-2 text-sm text-gray-700"
                    >
                      BioBrain Studio
                    </Link>
                  </div>
                </div>
              )}
            </div>
            <span className="text-white/30">|</span>
            <Link
              href="/digital-twin"
              className="text-sm font-medium text-black px-4"
            >
              Digital Twin
            </Link>
            <span className="text-white/30">|</span>
            <Link
              href="/faq"
              className="text-sm font-medium text-black px-4"
            >
              FAQs
            </Link>
            <span className="text-white/30">|</span>
            <Link
              href="/contact"
              className="text-sm font-medium text-black px-4"
            >
              Contact
            </Link>
          </div>

          {/* Right Side Buttons */}
          <div className="flex items-center gap-3 ml-auto">
            <Link href="/contact">
              <Button
                variant="ghost"
                size="sm"
                className="hidden sm:inline-flex text-black hover:bg-transparent hover:text-black"
              >
                Book Consultation
              </Button>
            </Link>
            <Link href="/assessment">
              <Button
                size="sm"
                className="bg-primary hover:bg-primary/90 text-white border-0 rounded-full py-6 px-6"
              >
                Start Assessment
              </Button>
            </Link>
          </div>
        </div>
      </div>
    </nav>
  )
}
